    font-family: 'Montserrat', sans-serif;
  } */
  
  .icon {
    vertical-align: middle;
    line-height: 1px;
    font-size: 20px;
  }
  .icon_header {
    vertical-align: middle;
    line-height: 1px;
    font-size: 26px;
  }
  .icon, .icon_header {
    margin-right: 5px;
  }
  
  .close_icon {
    color: red;
    margin-left: 30px;
    margin-top: 10px;
    cursor: pointer;
  }
  
  .grid-container {
    display: grid;
    grid-template-columns: 260px 1fr 1fr 1fr;
    grid-template-rows: 0.2fr 3fr;
    grid-template-areas:
      'sidebar header header header'
      'sidebar main main main';
    height: 100vh;
  }
  
  /* Header  */
  .header {
    grid-area: header;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px 0 30px;
    box-shadow: 0 6px 7px -3px rgba(0, 0, 0, 0.35);
  }
  
  .menu-icon {
    display: none;
  }
  /* End Header  */
  
  /* Sidebar  */
  #sidebar {
    grid-area: sidebar;
    height: 100%;
    background-color: #263043;
    overflow-y: auto;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
  }
  
  .sidebar-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px 0px 30px;
    margin-bottom: 30px;
  }
  
  .sidebar-title > span {
    display: none;
  }
  
  .sidebar-brand {
    margin-top: 15px;
    font-size: 20px;
    font-weight: 700;
  }
  
  .sidebar-list {
    padding: 0;
    list-style-type: none;
  }
  
  .sidebar-list-item {
    padding: 20px 20px 20px 20px;
    font-size: 18px;
  }
  
  .sidebar-list-item:hover {
    background-color: rgba(255, 255, 255, 0.2);
    cursor: pointer;
  }
  
  .sidebar-list-item > a {
    text-decoration: none;
    color: #9e9ea4;
  }
  
  .sidebar-responsive {
    display: inline !important;
    position: absolute;
    /*
      we want the z-index of the sidebar higher so that
      the charts are not showing over the sidebar 
      on small screens
    */
    z-index: 12 !important;
  }
  
  /* End Sidebar  */
  
  
  /* Main  */  
  .main-container {
    grid-area: main;
    overflow-y: auto;
    padding: 20px 20px;
    color: rgba(255, 255, 255, 0.95);
  }
  
  .main-title {
    display: flex;
    justify-content: space-between;
  }
  
  .main-cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    margin: 15px 0;
  }
  
  .card {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 8px 15px;
    border-radius: 5px;
  }
  
  .card:first-child {
    background-color: #2962ff;
  }
  
  .card:nth-child(2) {
    background-color: #ff6d00;
  }
  
  .card:nth-child(3) {
    background-color: #2e7d32;
  }
  
  .card:nth-child(4) {
    background-color: #d50000;
  }
  
  .card-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .card-inner > .card_icon {
    font-size: 25px;
  }
  
  .charts {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    margin-top: 60px;
    height: 300px;
  }
  
  /* End Main  */
  
  
  /* Tablet screens (875px and below) */
  @media screen and (max-width: 875px) {
    .grid-container {
      grid-template-columns: 1fr;
      grid-template-rows: 0.2fr 3fr;
      grid-template-areas:
        'header'
        'main';
    }

    #sidebar {
      display: none;
    }

    .menu-icon {
      display: inline;
    }

    .sidebar-title > span {
      display: inline;
    }

    .main-cards {
      grid-template-columns: 1fr 1fr;
      gap: 15px;
    }

    .charts {
      grid-template-columns: 1fr;
      margin-top: 40px;
      height: auto;
    }

    .main-container {
      padding: 15px 15px;
    }

    .header {
      padding: 0 15px;
    }
  }

  /* Mobile screens (700px and below) */
  @media screen and (max-width: 700px) {
    .main-cards {
      grid-template-columns: 1fr;
      gap: 10px;
      margin-bottom: 0;
    }

    .charts {
      grid-template-columns: 1fr;
      margin-top: 30px;
      gap: 15px;
    }

    .card {
      padding: 12px 15px;
    }

    .card-inner {
      flex-wrap: wrap;
    }

    .main-container {
      padding: 10px 10px;
    }

    .header {
      padding: 0 10px;
      height: 55px;
    }

    .main-title {
      font-size: 1.3rem;
    }
  }

  /* Extra Small screens (537px and below) */
  @media screen and (max-width: 537px) {
    .main-cards {
      grid-template-columns: 1fr;
      gap: 8px;
    }

    .card {
      padding: 10px 12px;
      border-radius: 3px;
    }

    .card-inner {
      font-size: 0.9rem;
    }

    .card-inner > .card_icon {
      font-size: 20px;
    }

    .icon {
      font-size: 16px;
    }

    .icon_header {
      font-size: 20px;
    }

    .charts {
      margin-top: 20px;
      gap: 10px;
    }

    .main-title {
      font-size: 1.1rem;
    }

    .header {
      padding: 0 8px;
      height: 50px;
    }

    .main-container {
      padding: 8px 8px;
    }
  }

  /* Very small screens (426px and below) */
  @media screen and (max-width: 426px) {
    .main-cards {
      grid-template-columns: 1fr;
      gap: 6px;
    }

    .card {
      padding: 8px 10px;
      border-radius: 3px;
      min-height: 80px;
    }

    .card-inner {
      font-size: 0.8rem;
    }

    .icon {
      font-size: 14px;
    }

    .icon_header {
      font-size: 16px;
    }

    .charts {
      margin-top: 15px;
      height: auto;
    }

    .main-title {
      font-size: 0.95rem;
    }

    .main-container {
      padding: 5px;
    }

    .header {
      padding: 0 5px;
      height: 45px;
    }
  }  